//Vars
$color-dark: #212121;
$color-light: #F8F8FF;
$primary-border: 2px solid $color-dark;
$tr-time: 0.4s;
$primary-font: "Lato", sans-serif;
$grid-gap: 1rem;
$bp-desktop-sm: 1100px;
$bp-tablet: 768px;

//Global
html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  background: $color-light;
  color: $color-dark;
  font-family: $primary-font;
  font-optical-sizing: auto;
  font-size: 16px;
  margin: 0;
}

h1, h2, h3, h4 {
  line-height: 1;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2.5rem;
}

h3 {
  font-size: 2.25rem;
}

h4 {
  font-size: 1.5rem;
}

p {
  line-height: 1.5rem;
  margin: 1rem 0 0;
}

.container {
  margin: 0 auto;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  width: 100%;

  @media screen and (min-width: $bp-tablet) {
    padding-left: 2rem;
    padding-right: 2rem;
  }

  @media screen and (min-width: $bp-desktop-sm) {
    padding-left: 2.25rem;
    padding-right: 2.25rem;
  }
}

//Utilities
.font-light {
  font-weight: 300;
}

//Buttons
.btn {
  align-items: center;
  border: $primary-border;
  color: $color-dark;
  display: flex;
  font-weight: 700;
  justify-content: center;
  letter-spacing: 0.1rem;
  line-height: 1;
  min-width: 160px;
  overflow: hidden;
  padding: .5rem;
  position: relative;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  transition: background-color $tr-time ease, 
  color $tr-time ease;
  width: 100%;

  &::before {
    background: $color-dark;
    content: '';
    height: 100%;
    left: -100%;
    position: absolute;
    top: 0;
    transition: left $tr-time ease;
    width: 100%;
    z-index: 0;
  }

  span {
    display: block;
    position: relative;
    z-index: 1;
  }

  svg {
    position: relative;
    left: 0;
    transition: left $tr-time ease;
    width: 1.5rem;
  }

  @media screen and (min-width: $bp-tablet) {
    width: auto;
  }

  &:hover {
    color: #FFF;

    &::before {
      left: 0;
    }
    
    svg {
      left: 0.25rem;
    }
  }
}

//Header
.header {
  position: relative;

  &::after {
    animation-name: width100;
    animation-duration: 1s;
    animation-delay: $tr-time;
    animation-fill-mode: forwards;
    background: $color-dark;
    bottom: 0;
    content: '';
    height: 3px;
    position: absolute;
    width: 0;
  }

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    padding-bottom: 2rem;
    padding-top: 1.5rem;

    @media screen and (min-width: $bp-tablet) {
      flex-direction: row;
    }
  }

  h1, h2 {
    margin: 0;
  }

  &__titles {
    text-align: center;

    @media screen and (min-width: $bp-tablet) {
      text-align: left;
    }
  }

  &__links {
    display: grid;
    gap: $grid-gap;
    grid-template-columns: 1fr;
    margin-top: 1.5rem;
    width: 100%;

    @media screen and (min-width: $bp-tablet) {
      grid-template-columns: repeat(2, 1fr);
      margin: 0;
      width: auto;
    }
  }
}

//Intro
.intro {
  .container {
    padding-top: 2.5rem;
  }

  h3 {
    margin: 0;
  }

  p {
    line-height: 2rem;
    @media screen and (min-width: $bp-tablet) {
      font-size: 1.25rem;
      max-width: 85%;
    }
  }
}

//Work 
.work {
  .container {
    min-height: 600px;
    padding-bottom: 2.5rem;
    padding-top: 2.5rem;
  }
  
  &__card-container {
    display: flex;
    flex-wrap: nowrap;
    overflow-x: scroll;
    scroll-snap-type: x mandatory;
    scroll-behavior: smooth;
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: none;
    scrollbar-width: none;
    &::-webkit-scrollbar {
      display: none;
    }

    @media screen and (min-width: $bp-tablet) {
      display: grid;
      gap: $grid-gap;
      grid-template-columns: 1fr;
      width: 100%;
    }

    @media screen and (min-width: $bp-tablet) {
      grid-template-columns: repeat(2, 1fr);
    }

    @media screen and (min-width: $bp-desktop-sm) {
      grid-template-columns: repeat(4, 1fr);
    }
  }
  
  &__card {
    border: $primary-border;
    flex: 0 0 100%;
    padding: 1.25rem 1.25rem 5.5rem;
    opacity: 1;
    position: relative;
    scroll-snap-align: center;
    transition: opacity 0.6s ease-out, transform 0.6s ease-out;
    width: 100%;
    
    @media screen and (min-width: $bp-tablet) {
      margin-right: 0;
      opacity: 0;

      &.fade-in {
        opacity: 1;
      }
    }

    figure {
      background: $color-dark;
      border: $primary-border;
      margin: 0 auto;
      height: 200px;
      width: 100%;

      img {
        display: block;
        height: 100%;
        object-fit: cover;
        width: 100%;
      }
    }

    h4 {
      margin: 1.5rem 0 0;
      padding-bottom: 1rem;
      position: relative;

      &::after {
        bottom: 0;
        background: $color-dark;
        content: '';
        height: 1px;
        left: 0;
        position: absolute;
        width: 20%;
      }
    }

    p {
      line-height: 1.5rem;
    }

    .btn {
      bottom: 1.25rem;
      left: 0;
      margin: auto;
      position: absolute;
      right: 0;
      width: 90%;
    }
  }
  
  &__card-nav {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-top: 1rem;

    @media screen and (min-width: $bp-tablet) {
      display: none;
    }

    button {
      background: transparent;
      border: 0;
      color: $color-dark;
      padding: 0.5rem;
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: background-color $tr-time ease;
      
      &:disabled {
        opacity: 0.3;
        cursor: not-allowed;
      }
    }
    
    .slide-btn {
      &.prev-slide {
        transform: rotate(180deg);
      }
    }
  }
}

//Footer
.footer {
  background: $color-dark;
  padding-bottom: 5rem;
  
  @media screen and (min-width: $bp-tablet) {
    margin-top: 2.5rem;
    padding-bottom: 10rem;
  }

  hr {
    background: $color-dark;
    border: 0;
    height: 3px;
  }
}

@keyframes width100 {
  0% { width: 0px; }
  100% { width: 100%; }
}